/* This usually goes in the global but let's keep it here
   for the sake of this tutorial */
   body {
    width: 100vw;
    height: 100vh;
    margin: 0;
    padding: 0;
    overflow: hidden;
  }
  
  /*  Make our map take the full viewport - 100% */
  #root,
  .App,
  .mapContainer,
  .mapBox {
    width: 100%;
    height: 100%;
  }
  
  /* Tooltip code */
  .mapboxgl-popup {
    font-family: "Baloo Thambi 2", cursive;
    font-size: 10px;
    padding: 0;
    margin: 0;
    color: #424242;
  }
  
  .mapboxgl-popup-content {
    padding: 1rem;
    margin: 0;
    max-width: 200px;
  
    > * {
      margin: 0 0 0.5rem;
      padding: 0;
    }
  
    p {
      border-bottom: 1px solid rgba(black, 0.2);
  
      b {
        font-size: 1.6rem;
        color: #212121;
        padding: 0 5px;
      }
    }
  
    img {
      width: 4rem;
      height: 4rem;
    }
  }

  .information {
    position: absolute;
    top: 0;
    left: 0;
    padding: 10px;
  }

  .information .map-overlay {
    position: absolute;
    background-color: #fff;
    margin-right: 20px;
    position: relative;
    font-family: Arial, sans-serif;
    overflow: auto;
    padding: 10px;
    border-radius: 3px;
  }

  .information .map-overlay h2 {
    margin: 10px;
    font-size: 1.2em;
  }

  .information .map-overlay p {
    font-size: 0.85em;
    margin: 10px;
    text-align: left;
  }

  .information .map-overlay input {
    background-color: transparent;
    display: inline-block;
    width: 100%;
    position: relative;
    margin: 0;
    cursor: ew-resize;
    }

  .information .legend {
    background-color: #fff;
    border-radius: 3px;
    box-shadow: 0 1px 2px rgba(0, 0, 0, 0.1);
    font: 16px/24px 'Helvetica Neue', Arial, Helvetica, sans-serif;
    padding: 10px;
    position: absolute;
    margin-top: 10px;
    z-index: 1;
    }
     
    .information .legend h4 {
    margin: 0 0 10px;
    }
     
    .information .legend div span {
      border-radius: 50%;
      display: inline-block;
      height: 10px;
      margin-right: 5px;
      width: 10px;
    }

    .filter-group {
      font: 12px/20px 'Helvetica Neue', Arial, Helvetica, sans-serif;
      font-weight: 600;
      position: absolute;
      top: 10px;
      right: 10px;
      z-index: 1;
      border-radius: 3px;
      width: 120px;
      color: #fff;
      }
    
    .filter-group input[type='checkbox']:first-child + label {
      border-radius: 3px 3px 0 0;
    }
         
    .filter-group label:last-child {
      border-radius: 0 0 3px 3px;
      border: none;
    }
         
    .filter-group input[type='checkbox'] {
      display: none;
    }

    .filter-group input[type='checkbox'] + label {
      background-color: #3386c0;
      display: block;
      cursor: pointer;
      padding: 10px;
      border-bottom: 1px solid rgba(0, 0, 0, 0.25);
      }

      .filter-group input[type='checkbox'] + label:hover,
      .filter-group input[type='checkbox']:checked + label {
      background-color: #4ea0da;
      }
       
      .filter-group input[type='checkbox']:checked + label:before {
      content: 'âœ”';
      margin-right: 5px;
      }

      .markerHospital {
        background-image: url('./icon/hospitalIcon.png');
        background-size: cover;
        width: 30px;
        height: 30px;
        border-radius: 50%;
        cursor: pointer;
      }
      .markerOutbreak {
        background-image: url('./icon/outbreakIcon.png');
        background-size: cover;
        width: 30px;
        height: 30px;
        border-radius: 50%;
        cursor: pointer;
      }
      